<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddQuestion" class="modal-title">{{ $t('AddQuestion') }}</h4>
                    <h4 v-if="EditQuestion" class="modal-title">{{ $t('EditQuestion') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" ref="AddQuestionnaire" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-ListofQuestionAnsweringType="{ data }">
                            <div class="row">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-2">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>{{$t('ListofQuestionAnsweringType')}}</span> </h5>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div v-for="(question, index) in listQuestions" :key="index" class="col-12 float-left applyalternatebg">
                                    <div class="row question-answer-list div_question_answer mb-3">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                            <div class="form-group">
                                                <label> {{ $t('FeedbackQuestion') }}:</label>
                                                <Field :name="'ListQuestions' + index + '.QuestionText'" :rules="{ 'required': true }" v-slot="{ errors }">
                                                    <input v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" v-model="question.QuestionText" :name="'ListQuestions' + index + '.QuestionText'" maxlength="200" type="text" placeholder="Select Feedback Question" />
                                                    <span v-if="errors[0]" ::name="'ListQuestions' + index + '.QuestionText'" class="invalid-feedback d-block">{{ $t('FeedbackQuestionRequired') }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                            <div class="form-group">
                                                <div class="col-12 p-0 float-left DivAnsweringType">
                                                    <label> {{ $t("AnsweringType") }}:</label>
                                                    <Field :name="'ListQuestions' + index + '.AnswerType'" :rules="{ 'required': false }">
                                                        <div class="input-group">
                                                            <select v-model="question.AnswerType" class="form-control" placeholder="Select Feedback Question" @change="answeringType(question.AnswerType,index)">
                                                                <option selected>{{ $t('SelectAnsweringType') }}</option>
                                                                <option v-for="(prty, prtyIndex) in AnsweringType" :key="prtyIndex" v-bind:value="prty.value">
                                                                    {{ prty.Name }}
                                                                </option>
                                                            </select>
                                                            <div class="input-group-append">
                                                                <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                    <a v-if="index === 0" href="javascript:void(0)" class="addquestionbox round-icon-small btn-success-light adinvc" title="Add More" @click="AddMore('approvarchain')">
                                                                        <i class="fa fa-plus pt-7 font-18"></i>
                                                                    </a>
                                                                    <a href="javascript:void(0)" class="round-icon-small btn-danger-light deletequestionbox" :class="{ 'd-none': index === 0 }" title="Delete" @click="deleteQuestion(index)">
                                                                        <i class="fa fa-trash"></i>
                                                                    </a>
                                                                </span>
                                                            </div>
                                                        </div>
                                                        <div class="objectiveAnswers form-group w-100 mt-2" v-if="question.objectiveAnswers">
                                                            <ul class="options liststylenone pl-0">
                                                                <li>
                                                                    <div class="custom-control custom-radio">
                                                                        <input type="radio" :id="'OptionName_' + index + '_TwoPointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="1" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'TwoPointScale'">
                                                                        <label :for="'OptionName_' + index + '_TwoPointScale'" class="mb-0">Two Point Scale</label>
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                    <div class="custom-control custom-radio">
                                                                        <input type="radio" :id="'OptionName_' + index + '_ThreePointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="2" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'ThreePointScale'">
                                                                        <label :for="'OptionName_' + index + '_ThreePointScale'" class="mb-0">Three Point Scale</label>
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                    <div class="custom-control custom-radio">
                                                                        <input type="radio" :id="'OptionName_' + index + '_FivePointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="3" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'FivePointScale'">
                                                                        <label :for="'OptionName_' + index + '_FivePointScale'" class="mb-0">Five Point Scale</label>
                                                                    </div>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                    </Field>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>

                    </dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        AddQuestionId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            AddQuestion: true,
            EditQuestion: false,
            listQuestions: [],
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "four",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('FeedbackName'),
                            name: "FeedbackName",
                            value: "",
                            placeholder: this.$t('Enter Feedback Name'),
                            validationRules: {
                                "required": true
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: this.$t('Enter Status'),
                            config: {
                                options: []
                            },
                            validationRules: {
                                "required": false
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Type'),
                            name: "Type",
                            value: 'Hiring',
                            placeholder: this.$t('Enter Type'),
                            config: {
                                options: []
                            },
                            validationRules: {
                                "required": false
                            },
                            disabled: true,
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('AdditionalComments'),
                            name: "AdditionalComments",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.chkIsAdditionalComments
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        name: "Description",
                        value: "",
                        visibility: false,
                        config: {
                            rows: 5,
                            cols: 7
                        },
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        label: '',
                        name: "ListofQuestionAnsweringType",
                        value: "",
                        visibility: true,
                        placeholder: "",
                        validationRules: "",
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            AnsweringType: [{
                    Name: "Comments",
                    value: 'Comments'
                },
                {
                    Name: "Star Rating",
                    value: 'Star Rating'
                },
                {
                    Name: "Objective",
                    value: 'Objective'
                }
            ],
            companyId: null,
            userId: null,
            FeedbackData: [],

        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetStatusList();
        vm.GetModuleList();
        var QuestionObj = {
            QuestionId: 0,
            TicketFeedbackId: 0,
            QuestionText: '',
            AnswerType: '',
            OptionName: '',
            OptionValue: '',
            objectiveAnswers: false
        };
        vm.listQuestions.push(QuestionObj);
        if (vm.AddQuestionId != null) {
            vm.EditFeedback();
        }
    },
    methods: {
        EditFeedback() {
            var vm = this;  
            var params = `Id=${vm.AddQuestionId}`
            DataService.GetFeedBackExitDetails(params).then((response) => {      
                if(response.data.status = 'Success'){                   
                    vm.FeedbackData = response.data.result.DATA[0];                    
                    vm.FormSchema[0].Data[0].value = vm.FeedbackData.TICKET_FEEDBACK_NAME;
                    vm.FormSchema[0].Data[1].value = vm.FeedbackData.STATUS_ID;
                    vm.FormSchema[0].Data[2].value = vm.FeedbackData.TYPE_CODE;
                    vm.FormSchema[0].Data[3].value = vm.FeedbackData.IS_ADDITIONAL_COMMENTS;
                    vm.FormSchema[1].Data[0].value = vm.FeedbackData.ADDITIONAL_COMMENTS;
                    if (vm.FormSchema[0].Data[3].value == true) {
                        vm.FormSchema[1].Data[0].visibility = true;
                    }else {
                        vm.FormSchema[1].Data[0].visibility = false
                    }
                    vm.QuestionData = response.data.result.QDATA;
                    vm.listQuestions = vm.QuestionData.map(item => ({
                        QuestionText: item.QUESTTION_TEXT,
                        AnswerType: item.ANSWER_TYPE
                    }));
                }
                vm.AddQuestion = false;
                vm.EditQuestion = true;
            })
        },
        AddMore: function () {
            var vm = this;
            const obj = {
                QuestionId: 0,
                TicketFeedbackId: 0,
                QuestionText: '',
                AnswerType: '',
                OptionName: '',
                OptionValue: '',
                objectiveAnswers: false
            };
            vm.listQuestions.push(obj);
        },
        deleteQuestion(index) {
            var vm = this;
            if (index !== 0) {
                vm.listQuestions.splice(index, 1);
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddQuestionnaire.validateForm.validate().then(result => {
                if (result) {
                    vm.isLoading = false;
                    var qlistQuestions = [];
                    var alistQuestions = [];
                    vm.listQuestions.forEach(function (item, key) {
                        var qobj = {
                            QuestionId: key + 1,
                            TicketFeedbackId: 0,
                            QuestionText: item.QuestionText,
                            AnswerType: item.AnswerType,
                            OptionName: item.OptionName,
                            OptionValue: item.OptionValue
                        }
                        var aobj = {
                            TicketId: 0,
                            TicketFeedbackId: 0,
                            QuestionId: key + 1,
                            TicketFeedbackAnswerId: 0,
                            FeedbackAnswer: 0,
                            QuestionText: item.QuestionText,
                            AnswerType: item.AnswerType,
                            OptionName: item.OptionName,
                            OptionValue: item.OptionValue,
                            Status_Id: vm.FormSchema[0].Data[1].value,
                            ThreadId: 0
                        }
                        qlistQuestions.push(qobj);
                        alistQuestions.push(qobj);
                    });
                    var data = {
                        TicketFeedbackId: vm.AddQuestionId != null ? vm.AddQuestionId : 0,
                        FeedbackName: vm.FormSchema[0].Data[0].value,
                        CompanyId: vm.companyId,
                        IsAdditionalComments: vm.FormSchema[0].Data[3].value,
                        AdditionalComments: vm.FormSchema[1].Data[0].value,
                        StatusId: vm.FormSchema[0].Data[1].value,
                        TypeCode: vm.FormSchema[0].Data[2].value,
                        CreatedBy: vm.userId,
                        ListQuestions: qlistQuestions,
                        ListAnswers: alistQuestions
                    }     
                    var params = JSON.stringify(data);
                    DataService.AddFeedbackk(params).then((response) => {
                        if(response.data.status == 'Success') {
                            if (response.data.result == '0') {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Feedback')]), "success", true, vm.$t("Alert"));
                        }
                        else if (response.data.result == '1') {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Feedback')]), "success", true, vm.$t("Alert"));
                        }
                        }                       
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Feedback")]), "failure", true, vm.$t("Alert"));
                        }
                        vm.$parent.FetchData();
                        vm.Close();
                    })
                }
                vm.isLoading = false;
            })
        },
        GetStatusList: function () {
            var vm = this;
            var url = `statusType=Hiring&hasglobal=true&langCode=en`
            DataService.GetStatusList(url).then((response) => {
                if (response.data.length > 0) {
                    var statusData = response.data;
                    statusData.forEach(element => {
                        var Obj = {
                            name: element.STATUS,
                            value: element.STATUS_ID
                        }
                        vm.FormSchema[0].Data[1].config.options.push(Obj);
                    });
                    vm.$refs.AddQuestionnaire.UpdateKeyValue();
                }
            });
        },
        GetModuleList: function () {
            var vm = this;
            DataService.GetModuleList().then((response) => {
                if (response.data.result.length > 0) {
                    var statusData = response.data.result;
                    statusData.forEach(element => {
                        var Obj = {
                            name: element.MODULE_NAME,
                            value: element.MODULE_NAME
                        }
                        vm.FormSchema[0].Data[2].config.options.push(Obj);
                    });
                    vm.$refs.AddQuestionnaire.UpdateKeyValue();
                }
            });
        },
        chkIsAdditionalComments: function (x, y, z) {
            var vm = this;
            if (y.value) {
                vm.FormSchema[1].Data[0].visibility = true;
            } else {
                vm.FormSchema[1].Data[0].visibility = false
            }
        },
        answeringType(answerType, i) {
            var vm = this;
            if (answerType == "Objective") {
                vm.$set(vm.listQuestions[i], 'objectiveAnswers', true);
            } else {
                vm.$set(vm.listQuestions[i], 'objectiveAnswers', false);
            }
        },
        Close: function () {
            this.$parent.ShowAddQuestion = false;
        }
    }
}
</script>
