<template lang="en">
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 v-if="AddSpecialization" class="modal-title">{{ $t('AddPostGraduation') }}</h4>
                        <h4 v-if="EditSpecialization" class="modal-title">{{ $t('EditPostGraduation') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                        <div class="left-content row flex-row-reverse mt-3">
                            <div class="col-lg-12 text-left">
                                <div class="py-2 mandatory">
                                    {{ $t('MandatoryString') }}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    
        
    <script>
    import DataService from '../../../services/DataService'
    export default {
        props: {
            EducationId: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                EditSpecialization: false,
                AddSpecialization: true,
                isLoading: false,
                FormSchema: [{
                        layoutType: "double",
                        Data: [{
                            astype: "TextField",
                            label: this.$t('PostGraduation'),
                            name: "Specialization",
                            value: "",
                            placeholder: this.$t('EnterPostGraduation'),
                            validationRules: {
                                "required": true
                            },
                            disabled: false,
                        }, ]
                    },
                    {
                        layoutType: "single",
                        Data: [{
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('Enter Description'),
                            validationRules: {
                                "required": false
                            },
                        }]
                    }
                ],
                buttons: [{
                        type: "submit",
                        class: "btn btn-success",
                        text: "<i class='fa fa-save pr-2'></i>Save",
                        isVisible: true,
    
                    },
                    {
                        type: "button",
                        class: "btn btn-danger",
                        text: "<i class='fa fa-close pr-2'></i>Cancel",
                        onClick: this.Close,
                    },
                ],
                companyId: null,
                userId: null
            }
        },
        created: function () {
            var vm = this;
            vm.userId = vm.GetUserInfo.ID;
            vm.companyId = vm.GetUserInfo.CompanyId;
            if (vm.EducationId != null) {
                vm.FormSchema[0].Data[0].value = vm.EducationId.NAME;
                vm.FormSchema[1].Data[0].value = vm.EducationId.desc;
                vm.AddSpecialization = false;
                vm.EditSpecialization = true;
            }
        },
        methods: {
            onSubmit(formfields, val) {
                var vm = this;
                var jsonobj = {
                    userId: vm.userId,
                    companyId: vm.companyId,
                    educationname: formfields.Specialization,
                    description: formfields.Description,
                    type: 'POST_GRADUATION',
                    educationid: vm.EducationId != null ? vm.EducationId.EDUCATION_ID : null
                };
                var params = JSON.stringify(jsonobj);
                DataService.SaveEducationQualification(params).then((response) => {
                    if (response.data.Status == "Success") {
                        if (response.data.Code == 0) {
                            vm.ShowAlert(vm.$t('PostGraduationAlreadyExist', [vm.$t('PostGraduation')]), "warning", true, vm.$t("Alert"));
                        } else {
                            if (vm.EducationId != null) {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('PostGraduation')]), "success", true, vm.$t("Alert"));
                            } else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('PostGraduation')]), "success", true, vm.$t("Alert"));
                            }
                            vm.$parent.FetchData();
                            vm.Close();
                        }
                    }
                });
    
            },
            Close: function () {
                this.$parent.ShowAddGraduation = false;
            }
    
        }
    }
    </script>
    