<template>
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('ManageDepartment') }}</h4>
                    <button type="button" class="close" v-on:click="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="HR_JOB" :LegendArray="[]"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('ManageDepartment')"
                    :widgets="[]" IdentityColumn="DEPARTMENT_ID" :listType="['List']" :ListData="Department"
                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                    :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                    :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                    v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'DEPARTMENT_NAME'">
                                <a href="#" @click="openAddDepartment(data.row)">{{data.row.DEPARTMENT_NAME }}</a>
                            </template>
                        </template>
                    </tg-list>
                    <dynamic-form lang="en" :buttons="buttons"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
    <AddDepartment v-if="ShowAddDepartment" :DepartmentId="DepartmentId"></AddDepartment>
</div>
</template>
    
<script>
import AddDepartment from './AddDeppartment.vue'
import Schema from './ManageDepartmentSchema'
import DataServices from './../../../services/DataService'
export default {
    props: {
        dField: {
            type: Object,
            required: true
        },
        dFieldId: {
            type: Object,
            required: true
        }
    },
    components: {
        AddDepartment
    },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            ShowAddDepartment: false,
            listheaderbuttons: Schema.buttons,
            Headers: [{
                    "COLUMN_NAME": "DEPARTMENT_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Department',
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "DEPARTMENT_DESC",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Department Description',
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "IS_VISIBILE_TO_CLIENT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Client Visibility',
                    "DISPLAY_ORDER": 3
                }
            ],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            Department: [],
            CurrentPage: 1,
            TotalPages: 0,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "departmentname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            DepartmentId: 0,
            buttons: [{
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created: function () {
        var vm = this;
        this.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();
    },
    methods: {
        clickEventOfColumn: function (row, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'DEPARTMENT_NAME') {
                vm.$router.push({
                    path: '/JobPost/JobDetail/' + row.DEPARTMENT_ID + '/HR/HR_JOB'
                });
            }
        },
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddDepartment":
                    this.openAddDepartment();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddDepartment: function (item) {
            var vm = this;
            vm.DepartmentId = item == undefined ? null : item;
            vm.ShowAddDepartment = true;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_DEPARTMENT&key=DEPARTMENT_ID`;
                    DataServices.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Department')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Department')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.searchCondition != '') {
                var params = `companyId=${vm.companyId}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            } else {
                var params = `companyId=${vm.companyId}&departmentname=&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            }
            DataServices.ManageDepartment(params).then((response) => {
                if (response.data.DATA.length > 0) {
                    vm.Department = response.data.DATA;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "DEPARTMENT_NAME") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vm.TotalRecords = response.data.DATA[0].TotalRecords;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled = true;
                } else {
                    vm.noRecord = false;
                    vm.Department = [];
                    vm.TotalRecords = 0
                }
                vm.isLoading = false;
            })
        },
        Close: function () {
            var vm = this;
            vm.$parent.GetDropDownData("HR_DEPT", vm.dField, vm.dFieldId, null);   
            vm.$parent.showManageDepartment = false;
        }
    }
}
</script>
