<template>
<div>
    <loader :is-visible="isLoading"></loader>
    <section class="main-content">
        <div ref="hellp" id="divMaindealData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ManageJobPost') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')" @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="JobPostForm">
                    <dynamic-form ref="ManageJobPostForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-Description="{ data }">
                            <ckeditor :editor="editor" v-model="noteDescription" :class="{ 'is-invalid': data.error != '' }">
                            </ckeditor>
                            <span class="invalid-feedback">{{ data.error }}</span>
                        </template>
                    </dynamic-form>
                </Form>
            </div>
        </div>
        <preview v-if="ShowPreview" :GetPreviewData="GetPreviewData"></preview>
        <ManageDesignation v-if="showManageDesignation" :dgField="dgField" :dgFieldId="dgFieldId"></ManageDesignation>
        <ManageDepartment v-if="showManageDepartment" :dField="dField" :dFieldId="dFieldId"></ManageDepartment>
        <ManageJobType v-if="showManageJobType" :JField="JField" :JFieldId="JFieldId"></ManageJobType>
        <ManageSkill v-if="ShowManageSkill" :SField="SField" :SFieldId="SFieldId"></ManageSkill>
        <ManageSpecialization v-if="ShowManageSpecialization" :SpField="SpField" :SpFieldId="SpFieldId"></ManageSpecialization>
        <ManageGraduation v-if="ShowManageGraduation" :gField="gField" :gFieldId="gFieldId"></ManageGraduation>
        <ManagePostGraduation v-if="ShowManagePostGraduation" :gpField="gpField" :gpFieldId="gpFieldId"></ManagePostGraduation>
        <ManageDoctrate v-if="ShowManageDoctrate" :DFieldData="DFieldData"></ManageDoctrate>
        <ManageIndustryType v-if="ShowManageIndustryType" :IField="IField" :IFieldId="IFieldId"></ManageIndustryType>
        <ManageFunctionalAreaType v-if="ShowManageFunctionalAreaType" :FieldData="FieldData" :FieldId="FieldId" :IsIndustryTypeId="IsIndustryTypeId" :key="reportcomponentkey"></ManageFunctionalAreaType>
        <ManageAddQuestion v-if="ShowManageAddQuestion" :QField="QField" :QFieldId="QFieldId"></ManageAddQuestion>
    </section>
</div>
</template>

    
<script>
import preview from './preview.vue';
import ManageDesignation from './ManageDesignation.vue';
import ManageDepartment from './ManageDepartment.vue';
import DataService from '../../../services/DataService';
import ManageJobType from './ManageJobType.vue';
import ManageSkill from './ManageSkill.vue';
import ManageSpecialization from './ManageSpecialization.vue';
import ManageGraduation from './ManageGraduation.vue';
import ManagePostGraduation from './ManagePostGraduation.vue';
import ManageDoctrate from './ManageDoctrate.vue';
import ManageIndustryType from './ManageIndustryType.vue';
import ManageFunctionalAreaType from './ManageFunctionalAreaType.vue';
import moment from 'moment';
import ManageAddQuestion from './ManageAddQuestion.vue';

export default {
    components: {
        preview,
        ManageDesignation,
        ManageDepartment,
        ManageJobType,
        ManageSkill,
        ManageSpecialization,
        ManageIndustryType,
        ManageFunctionalAreaType,
        ManageAddQuestion,
        ManageGraduation,
        ManagePostGraduation,
        ManageDoctrate
    },
    data() {
        return {
            isLoading: false,
            showManageDesignation: false,
            showManageDepartment: false,
            showManageJobType: false,
            ShowPreview: false,
            ShowManageSkill: false,
            ShowManageSpecialization: false,
            ShowManageGraduation:false,
            ShowManagePostGraduation: false,
            ShowManageDoctrate: false,
            ShowManageIndustryType: false,
            ShowManageFunctionalAreaType: false,
            ShowManageAddQuestion: false,
            FieldData: [],
            FieldId: 0,
            gField:[],
            gFieldId:0,
            gpField:[],
            gFieldId:0,
            dField:[],
            dFieldId:0,
            dgField:[],
            dgFieldId:0,
            graduationValue:0,
            PostGradValue:0,
            doctrateValue:0,
            IsIndustryTypeId: null,
            reportcomponentkey: 0,
            PostId: 0,
            FormSchema: [],
            GetPreviewData: [],
            DesignationName: [],
            editor: this.CLASSIC_EDITOR,
            noteDescription: '',
            LocationName: '',
            LocationDataName: [],
            currencyNameData: '',
            jobTypeName: '',
            skillItemData: [],
            DepartmentName: '',
            industryname: '',
            JField: [],
            JFieldId: 0,
            SField: [],
            SFieldId: 0,
            IField: [],
            IFieldId: 0,
            QField: [],
            QFieldId: 0,
            SpField: [],
            JobTitle:'',
            SDesc:'',
            FullDescription:'',
            SlaryFrom:'',
            SalaryTo:'',
            AboutComp:'',
            ExpFrom:'',
            ClDate:'',
            ExpTo:'',
            StDate:'',
            ContPerson:'',
            PHNumber:'',
            CompWebsite:'',
            Positions:'',
            CompName:'',
            SpFieldId: 0,
            DFieldData:[],
            buttons: [{
                    type: "submit",
                    class: "btn btn-info ",
                    text: "<i class='fa fa-eye pr-2'></i>Preview",
                    isVisible: true,
                    onClick: this.OnSubmit
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                    onClick: this.OnSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ]
        }
    },
    created: async function () {
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.PostId = this.$route.params.id == undefined ? 0 : this.$route.params.id;
        await this.getForm();
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        ManageAddQuestion() {
            var vm = this;
            vm.ShowManageAddQuestion = true;
        },
        ManageFunctionalAreaType() {
            var vm = this;
            if (vm.IsIndustryTypeId == null) {
                vm.ShowAlert(vm.$t("PleaseSelectIndustryBefore"), "warning", true, vm.$t("Alert"));
            } else {
                vm.reportcomponentkey += 1;
                vm.ShowManageFunctionalAreaType = true;
            }
        },
        ManageIndustryType() {
            var vm = this;
            vm.ShowManageIndustryType = true;
        },
        ManageSpecialization() {
            var vm = this;
            vm.ShowManageSpecialization = true;
        },
        ManageGraduation() {
            var vm = this;
            vm.ShowManageGraduation = true;
        },
        ManagePostGraduation() {
            var vm = this;
            vm.ShowManagePostGraduation = true;
        },
        ManageDoctrate() {
            var vm = this;
            vm.ShowManageDoctrate = true;
        },
        ManageSkill() {
            var vm = this;
            vm.ShowManageSkill = true;
        },
        ManageJobType() {
            var vm = this;
            vm.showManageJobType = true;
        },
        ManageDepartment() {
            var vm = this;
            vm.showManageDepartment = true;
        },
        ManageDesignation() {
            var vm = this;
            vm.showManageDesignation = true;
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
            }
        },
        getForm: async function () {
            var vm = this;
            var url = `recordId=${vm.PostId}&moduleName=Hiring&subModuleCode=HR_JOB&langCode=en`;
            await DataService.GetDynamicForm(url).then(function (response) {
                if (response.data != '') {
                    vm.FormSchema = vm.$refs.ManageJobPostForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    vm.FormSchema.forEach((group, gi) => {
                        group.Data.forEach(async (field, fi) => {
                            if (field.name == "skill_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageSkill;
                                vm.SField = field;
                                vm.SFieldId = field.value;
                            }
                            if (field.name == "designation_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageDesignation;
                                vm.dgField = field;
                                vm.dgFieldId = field.value;
                                if (field.value != '') {
                                    field.config.options.forEach(function (itm) {
                                        if (itm.value == field.value) {
                                            vm.DesignationName = itm.name;
                                        }
                                    });
                                }
                            }
                            if (field.name == "department_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageDepartment;
                                vm.dField = field;
                                vm.dFieldId = field.value;
                                if (field.value != '') {
                                    field.config.options.forEach(function (itm) {
                                        if (itm.value == field.value) {
                                            vm.DepartmentName = itm.name;
                                        }
                                    });
                                }
                            }
                            if (field.name == "job_type_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageJobType;
                                vm.JField = field;
                                vm.JFieldId = field.value;
                                if (field.value != '') {
                                    field.config.options.forEach(function (itm) {
                                        if (itm.value == field.value) {
                                            vm.jobTypeName = itm.name;
                                        }
                                    });
                                }
                            }
                            if (field.name == "crm_lead_industry_type_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageIndustryType;
                                vm.IField = field;
                                vm.IFieldId = field.value;
                                if (field.value != '') {
                                    vm.GetIndustryDropDown('HR_FUN_AREA', field.value);
                                }
                                if (field.value != '') {
                                    field.config.options.forEach(function (itm) {
                                        if (itm.value == field.value) {
                                            vm.industryname = itm.name;
                                        }
                                    });
                                }
                            }
                            if (field.name == "functional_area_id") {
                                field.config.showAddIcon = true;
                                field.config.options = [];
                                field.config.onAddButtonClick = vm.ManageFunctionalAreaType;
                            }
                            if (field.name == "specilization_stream") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageSpecialization;
                                vm.SpField = field;
                                vm.SpFieldId = field.value;
                            }
                            if (field.name == "graduation_stream") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageGraduation;
                            }
                            if (field.name == "post_graduation_stream") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManagePostGraduation;
                            }
                            if (field.name == "doctorate_stream") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageDoctrate;
                            }
                            if (field.name == "ticket_feedback_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.ManageAddQuestion;
                                vm.QField = field;
                                vm.QFieldId = field.value;
                            }
                            if (field.name == 'company_location_id') {
                                vm.locationItemData = [];
                                if (response.data.locationData.length > 0) {
                                    field.value = []; 
                                    response.data.locationData.forEach(location => {
                                        var locationvalue = location.value;
                                        var locationname = location.name;
                                        vm.locationItemData.push(locationname);
                                        field.value.push({
                                            label: locationname,
                                            value: locationvalue
                                        });
                                    });
                                    var commaSeparatedValues = field.value.map(item => item.value).join(',');
                                    field.commaSeparatedValues = commaSeparatedValues;
                                } else {
                                    field.value = field.value; 
                                }
                            }
                            if (field.name == 'skill_id') {
                                vm.skillItemData = [];
                                if (response.data.skillData.length > 0) {
                                    field.value = []; 
                                    response.data.skillData.forEach(skill => {
                                        var skillvalue = skill.value;
                                        var skillname = skill.name;
                                        vm.skillItemData.push(skillname);
                                        field.value.push({
                                            label: skillname,
                                            value: skillvalue
                                        });
                                    });
                                    var commaSeparatedValues = field.value.map(item => item.value).join(',');
                                    field.commaSeparatedValues = commaSeparatedValues;
                                } else {
                                    field.value = field.value; 
                                }
                            }
                            if (field.name == "start_date") {
                                field.config = {
                                    format: "MM/DD/YYYY",
                                    minDate: new Date()
                                };
                            }
                            if (field.name == "close_date") {
                                field.config = {
                                    format: "MM/DD/YYYY",
                                    minDate: new Date()
                                };
                            }
                            if (field.name == "exp_from") {
                                if (field.value != '') {
                                    vm.GetExpDropDown('HR_EXP_TO', field.value);
                                }
                            }
                            if (field.name == "graduation") {
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if (itm.name == 'I will choose Graduation') {
                                     vm.graduationValue = itm.value;
                                    }
                                });
                                }
                                vm.FormSchema[1].Data.forEach((field1, fi) => {
                                    if (field.value ==vm.graduationValue && field.value !='') {
                                        if (field1.name == "graduation_stream" && field.value == vm.graduationValue && vm.graduationValue !=0) {
                                            field1.visibility = true;
                                        }
                                    } else {
                                        if (field1.name == "graduation_stream") {
                                            field1.visibility = false;
                                        }
                                    }
                                });
                            }
                            if (field.name == "post_graduation") {
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if (itm.name == 'I will choose Post Graduation') {
                                     vm.PostGradValue = itm.value;
                                    }
                                });
                                }
                                vm.FormSchema[1].Data.forEach((field1, fi) => {
                                    if (field.value ==vm.PostGradValue && field.value !='') {
                                        if (field1.name == "post_graduation_stream" && field.value == vm.PostGradValue && vm.PostGradValue !=0) {
                                            field1.visibility = true;
                                        }
                                    } else {
                                        if (field1.name == "post_graduation_stream") {
                                            field1.visibility = false;
                                        }
                                    }
                                });
                            }

                            if (field.name == "doctorate") {
                                if(field.value !=''){
                                field.config.options.forEach(function (itm) {
                                 if (itm.name == 'I will choose Doctorate' || itm.name == 'I will choose Doct') {
                                     vm.doctrateValue = itm.value;
                                    }
                                });
                                }
                                vm.FormSchema[1].Data.forEach((field1, fi) => {
                                    if (field.value ==vm.doctrateValue && field.value !='') {
                                        if (field1.name == "doctorate_stream" && field.value == vm.doctrateValue && vm.doctrateValue !=0) {
                                            field1.visibility = true;
                                        }
                                    } else {
                                        if (field1.name == "doctorate_stream") {
                                            field1.visibility = false;
                                        }
                                    }
                                });
                            }
                            if (field.dtCode == "checkbox" && field.name == "specilization") {
                                vm.FormSchema[1].Data.forEach((field1, fi) => {
                                    if (field.value == '1') {
                                        if (field1.name == "specilization_stream" && field.value == '1') {
                                            field1.visibility = true;
                                        }
                                    } else {
                                        if (field1.name == "specilization_stream") {
                                            field1.visibility = false;
                                        }
                                    }
                                });
                            }
                            if (field.name == 'Description') {
                                vm.noteDescription = field.value;
                            }
                            field.config.onChange = vm.onChangeEventForFields;
                        })
                    });
                }
            });
        },
        onChangeEventForFields: function (x, y, z) {
            var vm = this;
            if (y.name == "crm_lead_industry_type_id") {
                vm.IsIndustryTypeId = y.value;
                vm.reportcomponentkey += 1;
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'functional_area_id') {
                            let id = y.value;
                            await vm.GetDropDownData('HR_FUN_AREA', field, id, null);
                            vm.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    })
                });
                vm.industryname = '';
                y.config.OptionsForBind.forEach(function (itm) {
                    if (itm.value == y.value) {
                        vm.industryname = itm.name;
                    }
                });
            }
            if (y.name == "start_date") {
                this.$delete(y, 'validationRules');
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "close_date") {
                            field.value = "";
                            field.config.minDate = x;
                            this.$delete(field, 'validationRules');
                        }
                    });
                });
            }
            if (y.name == "close_date") {
                this.$delete(y, 'validationRules');
            }
            if (y.name == "exp_from") {
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'exp_to') {
                            let id = y.value;
                            await vm.GetDropDownData('HR_EXP_TO', field, id, null);
                        }
                    })
                })
            }
            if (y.name == "specilization") {
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if ((y.value[0] != '' || y.value[1] !== '') && y.value.length>0) {
                            if (field.name == 'specilization_stream') {
                                field.visibility = true;
                            }
                        } else {
                            if (field.name == 'specilization_stream') {
                                field.visibility = false;
                            }
                        }

                    })
                })
            }
            if (y.name == "graduation") {
                y.config.options.forEach(function (itm) {
                if (itm.value == y.value) {
                    if(itm.name=='I will choose Graduation'){
                        vm.graduationValue = itm.value;
                    }
                }
                });
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (y.value[0] == vm.graduationValue || y.value[1] == vm.graduationValue && vm.graduationValue!=0) {
                            if (field.name == 'graduation_stream') {
                                field.visibility = true;
                                vm.gField=field;
                                vm.gFieldId= field.value;
                            }
                        } else {
                            if (field.name == 'graduation_stream') {
                                field.visibility = false;
                            }
                        }

                    })
                })
            }
            if (y.name == "post_graduation") {
                y.config.options.forEach(function (itm) {
                if (itm.value == y.value) {
                    if(itm.name=='I will choose Post Graduation'){
                        vm.PostGradValue = itm.value;
                    }
                }
                });
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (y.value[0] == vm.PostGradValue || y.value[1] == vm.PostGradValue && vm.PostGradValue!=0) {
                            if (field.name == 'post_graduation_stream') {
                                field.visibility = true;
                                vm.gpField=field;
                                vm.gpFieldId= field.value;
                            }
                        } else {
                            if (field.name == 'post_graduation_stream') {
                                field.visibility = false;
                            }
                        }

                    })
                })
            }
            if (y.name == "doctorate") {
                y.config.options.forEach(function (itm) {
                if (itm.value == y.value) {
                    if(itm.name=='I will choose Doctorate'|| itm.name == 'I will choose Doct'){
                        vm.doctrateValue = itm.value;
                    }
                }
                });
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (y.value[0] == vm.doctrateValue || y.value[1] == vm.doctrateValue && vm.doctrateValue!=0) {
                            if (field.name == 'doctorate_stream') {
                                field.visibility = true;
                                vm.DFieldData=field;
                            }
                        } else {
                            if (field.name == 'doctorate_stream') {
                                field.visibility = false;
                            }
                        }

                    })
                })
            }
            if (y.name == 'designation_id') {
                vm.DesignationName = '';
                y.config.OptionsForBind.forEach(function (itm) {
                    if (itm.value == y.value) {
                        vm.DesignationName = itm.name;
                    }
                });
            }
            vm.LocationName = '';
            if (y.name == 'company_location_id') {
                vm.LocationName = z.label;
                vm.LocationDataName.push(z.label);
            }
            if (y.name == 'currency') {
                y.config.OptionsForBind.forEach(function (itm) {
                    if (itm.value == y.value) {
                        vm.currencyNameData = itm.name;
                    }
                });
            }
            if (y.name == 'job_type_id') {
                vm.jobTypeName = '';
                y.config.OptionsForBind.forEach(function (itm) {
                    if (itm.value == y.value) {
                        vm.jobTypeName = itm.name;
                    }
                });
            }
            var skillName = '';
            if (y.name == 'skill_id') {
                skillName = z.label;
                vm.skillItemData.push(skillName);
            }
            if (y.name == 'department_id') {
                vm.DepartmentName = '';
                y.config.OptionsForBind.forEach(function (itm) {
                    if (itm.value == y.value) {
                        vm.DepartmentName = itm.name;
                    }
                });
            }
            if (y.name == 'salary_range_to') {
                var ToSalary = y.value.trim(); 
                vm.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == 'salary_range_from') {
                            if (!ToSalary || parseFloat(ToSalary) < parseFloat(field.value)) {
                                vm.ShowAlert(vm.$t("SalaryRang"), "warning", true, vm.$t("Alert"));
                                y.value = '';
                            }
                        }
                    });
                });
            }
        },
        GetDropDownData: async function (fieldCode, field, id, ths) {
            var ths = this;
            ths.FieldData = [];
            ths.FieldId = 0;
            if (field.name == 'functional_area_id') {
                ths.FieldData = field;
                ths.FieldId = id;
                var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
                await DataService.GetDDLData(url).then(function (response) {
                    if (response.data != '') {
                        field.config.options = [];
                        var json = response.data;
                        if (json != null) {
                            field.config.options = response.data.DATA;
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    } else {
                        field.config.options = [];
                    }
                });
            }
            if (field.name == 'exp_to') {
                var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
                await DataService.GetDDLData(url).then(function (response) {
                    if (response.data != '') {
                        field.config.options = [];
                        var json = response.data;
                        if (json != null) {
                            field.config.options = response.data.DATA;
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    } else {
                        field.config.options = [];
                    }
                });
            }
