        GetIndustryDropDown: async function (fieldCode, id) {
            var ths = this;
            var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
            await DataService.GetDDLData(url).then(function (response) {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'functional_area_id') {
                            field.value = field.value;
                            field.config.options = [];
                            if (response.data.DATA.length > 0) {
                                field.config.options = response.data.DATA;
                            }
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    })
                })
            });
        },
        GetExpDropDown: async function (fieldCode, id) {
            var ths = this;
            var url = `id=&moduleName=HIRING&type=${fieldCode}&search=&refId=${id}`;
            await DataService.GetDDLData(url).then(function (response) {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach(async (field, fi) => {
                        if (field.name == 'exp_to') {
                            field.value = field.value;
                            field.config.options = [];
                            if (response.data.DATA.length > 0) {
                                field.config.options = response.data.DATA;
                            }
                            ths.$refs.ManageJobPostForm.UpdateKeyValue();
                        }
                    })
                })
            });
        },
        Close: function () {
            this.$router.push({
                name: 'JobList'
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var inputString = val;
            var previewRegex = /<\/i>(.*)/;
            var previewMatch = inputString.match(previewRegex);
            var RequestFrom = previewMatch ? previewMatch[1] : "";
            vm.$refs.JobPostForm.validate().then(async result => {
                if (result) {
                    var CustomFieldArray = vm.$refs.ManageJobPostForm.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == 'skill_id') {
                            var val = "";
                            if (!vm.isBlank(x.field_value)) {
                                x.field_value.forEach((item, index) => {
                                    if (index > 0) {
                                        val += ','; 
                                    }
                                    val += item.value; 
                                });
                                x.field_value = val; 
                            }
                        }
                        if (x.field_name == 'company_location_id') {
                            var val = "";
                            if (!vm.isBlank(x.field_value)) {
                                x.field_value.forEach((item, index) => {
                                    if (index > 0) {
                                        val += ','; 
                                    }
                                    val += item.value; 
                                });
                                x.field_value = val; 
                            }
                        }
                        if (vm.PostId > 0) {
                            if (x.field_name == 'modified_by') {
                                x.field_value = vm.userId;
                            }
                        }
                        if (x.field_name == 'post_id') {
                            x.field_value = vm.PostId;
                        }
                        if (x.field_name == 'company_id') {
                            x.field_value = vm.companyId;
                        }
                        if (x.field_name == 'status_id') {
                            x.field_value = x.field_value;
                        }
                        if (x.field_name == 'open_to') {
                            x.field_value = 'E';
                        }
                        if (x.field_name == 'created_by') {
                            x.field_value = vm.userId;
                        }
                        if (x.field_name == 'created_at') {
                            x.field_value = vm.$options.filters.formatDate(new Date());
                        }
                        if (x.field_name == 'modified_at') {
                            x.field_value = vm.$options.filters.formatDate(new Date());
                        }
                        if (x.field_name == "specilization" || x.field_name == "is_visible_contact" || x.field_name == "visible_to_candidate") {
                            if (x.field_value > '0') {
                                x.field_value = '1';

                            } else {
                                x.field_value = '0';
                            }
                        }
                        if (x.field_name == 'Description') {
                            x.field_value = vm.noteDescription;
                        }
                    })
                    var postJSON = {
                        data: CustomFieldArray,
                    }
                    var objectPostString = JSON.stringify(postJSON);
                    if(RequestFrom=='Preview'){
                    let data=postJSON.data;
                    await data.forEach(function (item) {
                         if(item.field_name=="post_title"){
                            vm.Title= item.field_value;
                         }else if(item.field_name=="job_title"){
                            vm.JobTitle= item.field_value;
                         }else if(item.field_name=="salary_range_from"){
                            vm.SlaryFrom= item.field_value;
                         }else if(item.field_name=="salary_range_to"){
                            vm.SalaryTo= item.field_value;
                         }else if(item.field_name=="exp_from"){
                            vm.ExpFrom= item.field_value;
                         }else if(item.field_name=="exp_to"){
                            vm.ExpTo= item.field_value;
                         }else if(item.field_name== "start_date"){
                            vm.StDate= item.field_value;
                         }else if(item.field_name== "close_date"){
                            vm.ClDate= item.field_value;
                         }else if(item.field_name=="contact_person"){
                            vm.ContPerson= item.field_value;
                         }else if(item.field_name=="phone_number"){
                            vm.PHNumber= item.field_value;
                         }else if(item.field_name=="post_link"){
                            vm.CompWebsite= item.field_value;
                         }else if(item.field_name=="company_name"){
                            vm.CompName= item.field_value;
                         } else if(item.field_name=="about_company"){
                            vm.AboutComp= item.field_value;
                         } else if(item.field_name=="short_desc"){
                            vm.SDesc= item.field_value;
                         }else if(item.field_name=="Description"){
                            vm.FullDescription= item.field_value;
                         } else if(item.field_name=="positions"){
                            vm.Positions= item.field_value;
                         }   
                    });
                    var PreviewJSON = {
                    skillData : vm.skillItemData,
                    location :vm.LocationDataName,
                    postTitle :vm.Title,
                    jobTitle : vm.JobTitle,
                    position :vm.Positions,
                    experience_from: vm.ExpFrom,
                    experience_to : vm.ExpTo,
                    currency :vm.currencyNameData,
                    salaryRangeFrom :vm.SlaryFrom,
                    salaryRangeTo: vm.SalaryTo,
                    startDate : vm.$options.filters.formatDate(vm.StDate),
                    closeDate : vm.$options.filters.formatDate(vm.ClDate),
                    jobType : vm.jobTypeName,
                    designation : vm.DesignationName,
                    department : vm.DepartmentName,
                    industry : vm.industryname,
                    contactPerson :vm.ContPerson,
                    phoneNumber: vm.PHNumber,
                    companyWebsite : vm.CompWebsite,
                    companyName :vm.CompName,
                    boutCompany: vm.AboutComp,
                    shortdescription :vm.SDesc,
                    description:vm.FullDescription
                    }
                    vm.GetPreviewData=PreviewJSON;
                    vm.ShowPreview = true;
                    }else{
                    DataService.SaveHRJob(objectPostString).then((response) => {
                        if (response.data.Status == 'Success') {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("JobPost")]), "success", true, vm.$t("Alert"));
                            if (vm.PostId > 0) {
                                vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("JobPost")]), "success", true, vm.$t("Alert"));
                            }
                            vm.Close();
                        } else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("JobPost")]), "failure", true, vm.$t("Alert"));
                        }
                    })
                }
                }
            })
        }
    }
}
