<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('ManageJobType') }}</h4>
                    <button type="button" class="close" v-on:click="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="HR_JOB" :LegendArray="[]"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('ManageJobType')"
                    :widgets="[]" IdentityColumn="job_type_id" :listType="['List']" :ListData="JobtypeData"
                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                    :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                    :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                    v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'job_type_name'">
                                <a href="#" @click="openAddJobType(data.row)">{{data.row.job_type_name }}</a>
                            </template>
                        </template>
                    </tg-list>
                    <dynamic-form lang="en" :buttons="buttons"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
    <AddJobType v-if="ShowAddJobType" :JobTypeId="JobTypeId"></AddJobType>
</div>
</template>

    
<script>
import AddJobType from './AddJobType.vue'
import Schema from './ManageJobTypeSchema'
import DataServices from './../../../services/DataService'
export default {
    props: {
        JField: {
            type: Object,
            required: true
        },
        JFieldId: {
            type: Object,
            required: true
        }
    },
    components: {
        AddJobType
    },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            ShowAddJobType: false,
            listheaderbuttons: Schema.buttons,
            Headers: [{
                    "COLUMN_NAME": "job_type_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Job Type',
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "job_type_desc",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Job Description',
                    "DISPLAY_ORDER": 2
                },
            ],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            JobtypeData: [],
            CurrentPage: 1,
            TotalPages: 0,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "jobtypename",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            JobTypeId: 0,
            buttons: [{
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.Close,
            }, ],
        }
    },
    created: function () {
        var vm = this;
        this.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();
    },
    methods: {
        clickEventOfColumn: function (row, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'job_type_name') {
                vm.$router.push({
                    path: '/JobPost/JobDetail/' + row.job_type_id + '/HR/HR_JOB'
                });
            }
        },
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddJobType":
                    this.openAddJobType();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddJobType: function (item) {
            var vm = this;
            vm.JobTypeId = item == undefined ? null : item;
            vm.ShowAddJobType = true;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}`;
                    DataServices.DeleteJobTypes(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Pri_JobType')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Pri_JobType')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.searchCondition != '') {
                var params = `companyId=${vm.companyId}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            } else {
                var params = `id=&companyId=${vm.companyId}&jobtypename=&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            }
            DataServices.ManageJobType(params).then((response) => {
                if (response.data.DATA.length > 0) {
                    vm.JobtypeData = response.data.DATA;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "job_type_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vm.TotalRecords = response.data.DATA[0].TotalRecords;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled = true;
                } else {
                    vm.noRecord = false;
                    vm.JobtypeData = [];
                    vm.TotalRecords = 0
                }
                vm.isLoading = false;
            })
        },
        Close: function () {
            var vm = this;
            vm.$parent.GetDropDownData("HR_JOB_TYP", vm.JField, vm.JFieldId, null);   
            vm.$parent.showManageJobType = false;
        }
    }
}
</script>
