<template>
<section>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">Preview</h4>
                        <button type="button" class="close" @click="Close">
                            <i class="fa fa-times" aria-hidden="true"></i>
                        </button>
                    </div>
                    <div class="modal-body" style="max-height:400px;overflow-x:auto;">
                        <div class="card-view col-md-12 float-left px-0">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                                <div class="card-box float-left mb-0 w-100">
                                    <div class="cardv-body float-left pt-0 mt-0 w-100">
                                        <div class="card-text evemgmt_details p-2 details-iconfixes">
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-building-o" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('PostTitle') }}:</span><span>{{ postTitle }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-briefcase" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('JobTitle') }}:</span><span>{{ jobTitle }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-user-o" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Positions') }}:</span><span>{{ position }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa fa-briefcase text-dark float-left pr-2" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Experience') }}:</span><span>{{ experience_from == "" ? 0 : experience_from }} -
                                                    {{ experience_to == "" ? 0 : experience_to }} Years</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa fa-money text-dark float-left pr-2" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Salary') }}:</span><span>{{ currency }} {{ salaryRangeFrom }}</span> -
                                                <span>{{ currency }} {{ salaryRangeTo }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa text-dark float-left pr-2 fa-calendar" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('StartDate') }}:</span><span>{{ startDate }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa text-dark float-left pr-2 fa-calendar" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('CloseDate') }}:</span><span>{{ closeDate }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-black-tie" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('JobType') }}:</span><span>{{ jobType }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-user-circle-o" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Designation') }}:</span><span>{{ designation }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-building-o" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Department') }}:</span><span>{{ department }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-slack" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('Industry') }}:</span><span>{{ industry }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa fa-user text-dark float-left pr-2" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('ContactPerson') }}:</span><span>{{ contactPerson }}</span>
                                            </div>
                                            <div v-if="phoneNumber != '' && phoneNumber != null" class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa fa-mobile text-dark float-left pr-2" style="font-size:18pt;"></em>
                                                <span class="float-left mr-3">{{ $t('PhoneNumber') }}:</span><span>{{ phoneNumber }}</span>
                                                <vue-phone-number-input id="phone" v-model="phoneNumber" valid-color="black" dark-color="black" color="black" :no-use-browser-locale="true" disabled></vue-phone-number-input>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-at" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('CompanyWebsite') }}:</span><span>{{ companyWebsite }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 col-lg-4 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2 fa fa-copyright" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('CompanyName') }}:</span><span>{{ companyName }}</span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 float-left px-2 py-2 d-flex align-items-center">
                                                <em class="fa fa-industry text-dark float-left pr-2" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('AboutCompany') }}:</span>{{boutCompany }}<span></span>
                                            </div>
                                            <div class="col-sm-12 col-md-12 float-left px-0 py-2 d-flex align-items-center">
                                                <em class="text-dark float-left pr-2" style="font-size:12pt;"></em>
                                                <span class="float-left mr-3">{{ $t('ShortDescription') }}:</span>{{ shortdescription }}<span></span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-12 mb-3 px-0 my-2 float-left">
                            <div class="heading-border">
                                <h5 class="h5"><span>{{ $t('JobDescription') }}</span></h5>
                            </div>
                            <p class="description">
                                <span v-html="description"></span>
                            </p>
                        </div>
                        <div class="col-lg-12 mb-3 px-0 float-left">
                            <div class="heading-border">
                                <h5 class="h5"><span>{{ $t('Skills') }}</span></h5>
                            </div>
                            {{ skillData }}
                        </div>
                        <div class="col-lg-12 mb-3 px-0 float-left">
                            <div class="heading-border">
                                <h5 class="h5"><span>{{ $t('Locations') }}</span></h5>
                            </div>
                            {{ location }}
                        </div>
                    </div>
                    <div class="border p-3">
                        <Form ref="PreviewForm">
                            <dynamic-form ref="ManagePreviewForm" :buttons="buttons"></dynamic-form>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
export default {
    props: {
        GetPreviewData: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            PostId: 0,
            skillData: [],
            location: [],
            buttons: [{
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.Close,
            }],
            postTitle: '',
            jobTitle: '',
            position: '',
            experience_from: '',
            experience_to: '',
            currency: '',
            salaryRangeFrom: '',
            salaryRangeTo: '',
            startDate: '',
            closeDate: '',
            jobType: '',
            designation: '',
            department: '',
            industry: '',
            contactPerson: '',
            phoneNumber: '',
            companyWebsite: '',
            companyName: '',
            boutCompany: '',
            shortdescription: ''
        }
    },
    created: async function () {
        this.PostId = this.$route.params.id == undefined ? 0 : this.$route.params.id;
        await this.getForm();
    },
    methods: {
        getForm: async function () {
            var vm = this;
            vm.GetPreviewData;
                var data = '';
                if (vm.GetPreviewData.location.length>0) {
                    vm.GetPreviewData.location.forEach((itm, index) => {
                        if (index == 0) {
                            data = itm;
                        }
                        else {
                            data += ',' + itm;
                        }
                    })
                     vm.location = data;
                    }else{
                    vm.location='';
                    }
                    var dataValue = '';
                    if (vm.GetPreviewData.skillData.length>0) {
                    vm.GetPreviewData.skillData.forEach((itm, index) => {
                        if (index == 0) {
                            dataValue = itm;
                        }
                        else {
                            dataValue += ',' + itm;
                        }
                    })
                     vm.skillData = dataValue;
                    }else{
                    vm.skillData='';
                    }
                let preData= vm.GetPreviewData
                    vm.postTitle =preData.postTitle;
                    vm.jobTitle = preData.jobTitle;
                    vm.position = preData.position;
                    vm.experience_from = preData.experience_from;
                    vm.experience_to = preData.experience_to;
                    vm.currency = preData.currency;
                    vm.salaryRangeFrom = preData.salaryRangeFrom;
                    vm.salaryRangeTo =preData.salaryRangeTo;
                    vm.startDate = preData.startDate;
                    vm.closeDate = preData.closeDate;
                    vm.jobType = preData.jobType;
                    vm.designation = preData.designation;
                    vm.department = preData.department;
                    vm.industry = preData.industry;
                    vm.contactPerson = preData.contactPerson;
                    vm.phoneNumber = preData.phoneNumber;
                    vm.companyWebsite =preData.companyWebsite;
                    vm.companyName = preData.companyName;
                    vm.boutCompany =preData.boutCompany;
                    vm.shortdescription = preData.shortdescription;
                    vm.description= preData.description;
        },
        Close: function () {
            var vm = this;
            vm.$parent.ShowPreview = false;
        },
    }
}
</script>
