<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddIndustry" class="modal-title">{{ $t('AddIndustry') }}</h4>
                    <h4 v-if="EditIndustry" class="modal-title">{{ $t('EditIndustry') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form ref="updateValues" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                    <div class="left-content row flex-row-reverse mt-3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

    
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        IndustryId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            EditIndustry: false,
            AddIndustry: true,
            FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Industry'),
                        name: "Industry",
                        value: "",
                        placeholder: this.$t('Enter Industry'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "SelectField",
                        label: this.$t('Icon'),
                        name: "Icon",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId: null,
            userId: null,
            IconData: []
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if (vm.IndustryId != null) {
            vm.FormSchema[0].Data[0].value = vm.IndustryId.crm_lead_industry_type;
            //vm.FormSchema[0].Data[1].value = vm.IndustryId.Icon;
            vm.FormSchema[1].Data[0].value = vm.IndustryId.crm_lead_industry_type_desc;
            vm.AddIndustry = false;
            vm.EditIndustry = true;
        }
        vm.GetIndustryIcon();
    },
    methods: {
        GetIndustryIcon(){
            var vm = this;
            var url=`id=`;
            DataService.GetIndustryIcons(url).then(function (response) {
                vm.IconData = response.data.DATA;
                vm.IconData.forEach(item => {
                    vm.FormSchema[0].Data[1].config.options.push({
                        name: item.icon_name,
                        value: item.icon_value
                    })
                })
                vm.$refs.updateValues.UpdateKeyValue();
            })
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                userId: vm.userId,
                companyId: vm.companyId,
                name: formfields.Industry,
                description: formfields.Description,
                icondata: formfields.Icon,
                parentid: null,
                id: vm.IndustryId != null ? vm.IndustryId.crm_lead_industry_type_id : 0
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveIndustry(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.IndustryId != null) {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Industry')]), "success", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Industry')]), "success", true, vm.$t("Alert"));
                    }
                    vm.$parent.FetchData();
                    vm.Close();
                } 
                if (response.data.status == 'AlreadyExixts') {
                    vm.ShowAlert(vm.$t('IndustryAlreadyExist', [vm.$t('Industry')]), "warning", true, vm.$t("Alert"));
                }
            });
        },
        Close: function () {
            this.$parent.ShowAddIndustry = false;
        }
    }
}
</script>
