<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddFunctionalArea" class="modal-title">{{ $t('AddFunctionalArea') }}</h4>
                    <h4 v-if="EditFunctionalArea" class="modal-title">{{ $t('EditFunctionalArea') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form ref="updateValues" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                    <div class="left-content row flex-row-reverse mt-3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        AreaGroupArr: {
            type: Object,
            required: true
        },
        AreaId: {
            type: Object,
            required: true
        },
        IsIndustryTypeId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            EditFunctionalArea: false,
            AddFunctionalArea: true,
            FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Functional Area'),
                        name: "FunctionalArea",
                        value: "",
                        validationRules: {
                            "required": true,
                            max: 100
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('description'),
                        name: "Description",
                        value: "",
                        validationRules: "",
                    }, ]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId: null,
            userId: null
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if (vm.AreaId > 0) {
            vm.FormSchema[0].Data[0].value = vm.AreaGroupArr.crm_lead_industry_type;
            vm.FormSchema[1].Data[0].value = vm.AreaGroupArr.crm_lead_industry_type_desc;
            vm.AddFunctionalArea = false;
            vm.EditFunctionalArea = true;
        }
    },
    methods: {
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                userId: vm.userId,
                companyId: vm.companyId,
                name: formfields.FunctionalArea,
                description: formfields.Description,
                parentid: vm.IsIndustryTypeId,
                id: vm.AreaId == null ? 0 : vm.AreaId,
                subModuleCode: "HR_JOB",
                fileName: '',
                moduleName: "Hiring",
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveFunctionalArea(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.AreaId > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("FunctionalArea")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    } else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("FunctionalArea")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchData();
                } else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("FunctionalArea")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            this.$parent.ShowAddFunctionalArea = false;
        }
    }
}
</script>
