<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddDepartment" class="modal-title">{{ $t('AddDepartment') }}</h4>
                    <h4 v-if="EditDepartment" class="modal-title">{{ $t('EditDepartment') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                    <div class="left-content row flex-row-reverse mt-3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        DepartmentId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            AddDepartment: true,
            EditDepartment: false,
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Department'),
                        name: "Department",
                        value: "",
                        placeholder: this.$t('Enter Department'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "CheckBoxField",
                        label: this.$t('isVisibleToClient'),
                        name: "isvisible",
                        value: "",
                        config: {
                            options: [{
                                value: "true",
                                name: ""
                            }]
                        },
                        validationRules: {
                            "required": false
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId: null,
            userId: null
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if (vm.DepartmentId != null) {
            vm.FormSchema[0].Data[0].value = vm.DepartmentId.DEPARTMENT_NAME;
            vm.FormSchema[1].Data[0].value = vm.DepartmentId.IS_VISIBILE_TO_CLIENT == 'Yes' ? 'true' : 'false';
            vm.FormSchema[2].Data[0].value = vm.DepartmentId.DEPARTMENT_DESC;
            vm.AddDepartment = false;
            vm.EditDepartment = true;
        }
    },
    methods: {
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                userId: vm.userId,
                companyId: vm.companyId,
                departmentname: formfields.Department,
                isvisible: formfields.isvisible == true ? 'true' : 'false',
                description: formfields.Description,
                departmentId: vm.DepartmentId != null ? vm.DepartmentId.DEPARTMENT_ID : 0
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveDepartment(params).then((response) => {
                if (response.data.Status == "Success") {
                    if (response.data.Code == 0) {
                        vm.ShowAlert(vm.$t('DepartmentAlreadyExist', [vm.$t('AddDepartment')]), "warning", true, vm.$t("Alert"));
                    } else {
                        if (vm.DepartmentId != null) {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('AddDepartment')]), "success", true, vm.$t("Alert"));
                        } else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('AddDepartment')]), "success", true, vm.$t("Alert"));
                        }
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                }
            });

        },
        Close: function () {
            this.$parent.ShowAddDepartment = false;
        }

    }
}
</script>
