<template>
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('ManageDesignation') }}</h4>
                    <button type="button" class="close" v-on:click="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="HR_JOB" :LegendArray="[]"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('ManageDesignation')"
                    :widgets="[]" IdentityColumn="designation_id" :listType="['List']" :ListData="Designation"
                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                    :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                    :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                    v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'designation_name'">
                                    <a href="#" @click="openAddDesignation(data.row)">{{data.row.designation_name }}</a>
                            </template>
                        </template>
                    </tg-list>
                    <dynamic-form lang="en" :buttons="buttons"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
    <AddDesignation v-if="ShowAddDesignation" :DesignationId="DesignationId"></AddDesignation>
</div>
</template>
    
<script>
import Schema from './ManageDesignationSchema'
import AddDesignation from './AddDesignation.vue'
import DataServices from './../../../services/DataService'
export default {
    props: {
        dgField: {
            type: Object,
            required: true
        },
        dgFieldId: {
            type: Object,
            required: true
        }
    },
    components: {
        AddDesignation
    },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            ShowAddDesignation: false,
            listheaderbuttons: Schema.buttons,
            Headers: [{
                    "COLUMN_NAME": "designation_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Designation Type',
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "designation_desc",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Designation Description',
                    "DISPLAY_ORDER": 2
                }
            ],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            Designation: [],
            CurrentPage: 1,
            TotalPages: 0,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "designationname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            DesignationId: 0,
            buttons: [{
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created: function () {
        var vm = this;
        this.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();
    },
    methods: {
        clickEventOfColumn: function (row, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'designation_name') {
                vm.$router.push({
                    path: '/JobPost/JobDetail/' + row.designation_id + '/HR/HR_JOB'
                });
            }
        },
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddDesignation":
                    this.openAddDesignation();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddDesignation: function (item) {
            var vm = this;
            vm.DesignationId = item == undefined ? null : item;
            vm.ShowAddDesignation = true;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_DESIGNATION&key=designation_id`;
                    DataServices.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Designation')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Designation')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.searchCondition != '') {
                var params = `companyId=${vm.companyId}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            } else {
                var params = `companyId=${vm.companyId}&designationname=&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            }
            DataServices.ManageDesignation(params).then((response) => {
                if (response.data.DATA.length > 0) {
                    vm.Designation = response.data.DATA;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "designation_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });    
                    vm.TotalRecords = response.data.DATA[0].TotalRecords;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled = true;
                } else {
                    vm.noRecord = false;
                    vm.Designation = [];
                    vm.TotalRecords = 0
                }
                vm.isLoading = false;
            })
        },
        Close: function () {
            var vm = this;
            vm.$parent.GetDropDownData("HR_DESIG", vm.dgField, vm.dgFieldId, null);   
            vm.$parent.showManageDesignation = false;
        }
    }
}
</script>
