<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('ManageAddQuestion') }}</h4>
                    <button type="button" class="close" v-on:click="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="HR_JOB" :LegendArray="[]"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('ManageAddQuestion')"
                    :widgets="[]" IdentityColumn="ticket_feedback_id" :listType="['List']" :ListData="AddQuestionData"
                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                    :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                    :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                    v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                    v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'ticket_feedback_name'">
                                <a href="#" @click="openAddQuestion(data.row.ticket_feedback_id)">{{data.row.ticket_feedback_name }}</a>
                            </template>

                            <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                                <!-- <listing-status :isfor="'HR_JOB'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'HR_JOB')" ref="updatestatus"  -->
                                <listing-status :isfor="'HR_JOB'"  ref="updatestatus" 
                                    v-bind:dataid="data.row.ticket_feedback_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" 
                                    data-action="index" v-bind:dataselected="data.row.STATUS_ID" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                                </listing-status>
                                <!-- <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUS_ID, 'HR_JOB')"> -->
                                <a class="inactive-disabled" >
                                    <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                                        <!-- {{data.row[data.column.COLUMN_NAME]}}-->
                                         {{ data.row.STATUS_NAME }} 
                                    </span>
                                </a>
                            </template>

                        </template>
                    </tg-list>
                </div>
            </div>
        </div>
    </div>
    <AddQuestion v-if="ShowAddQuestion" :AddQuestionId="AddQuestionId"></AddQuestion>
</div>
</template>
    
<script>
import AddQuestion from './AddQuestion.vue'
import Schema from './ManageAddQuestionSchema'
import DataServices from './../../../services/DataService'
export default {
    props: {
        QField: {
            type: Object,
            required: true
        },
        QFieldId: {
            type: Object,
            required: true
        }
    },
    components: {
        AddQuestion
    },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            ShowAddQuestion: false,
            listheaderbuttons: Schema.buttons,
            StatusListForListing: [],
            Headers: [{
                    "COLUMN_NAME": "ticket_feedback_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Feedback Name',
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": "DEPARTMENT_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Department',
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "type_code",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Type',
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "STATUS_ID",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Status',
                    "DISPLAY_ORDER": 4
                }
            ],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            AddQuestionData: [],
            CurrentPage: 1,
            TotalPages: 0,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "serach",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            AddQuestionId: 0,
        }
    },
    created: function () {
        var vm = this;
        this.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();
    },
    methods: {
        clickEventOfColumn: function (row, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'ticket_feedback_name') {
                vm.$router.push({
                    path: '/JobPost/JobDetail/' + row.ticket_feedback_id + '/HR/HR_JOB'
                });
            }
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
             
            DataServices.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddQuestion":
                    this.openAddQuestion(0);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddQuestion: function (item) {
            var vm = this;
            vm.AddQuestionId = item == 0 ? 0 : item;
            vm.ShowAddQuestion = true;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_ticket_feedback&key=ticket_feedback_id`;
                    DataServices.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Feedback')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Feedback')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.searchCondition != '') {
                var params = `companyId=${vm.companyId}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            } else {
                var params = `companyId=${vm.companyId}&serach=&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&Status=&TYPE=Hiring`;
            }
            DataServices.GetFeedBackListing(params).then((response) => {
                if (response.data != '') {
                    vm.AddQuestionData = JSON.parse(response.data).DATA;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "ticket_feedback_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if(column.COLUMN_NAME == 'STATUS_ID'){
                            column.settings = {
                                isInSlot : true
                            };
                        }
                    });
                    vm.TotalRecords = JSON.parse(response.data).DATA[0].TOTALRECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled = true;
                } else {
                    vm.noRecord = false;
                    vm.AddQuestionData = [];
                    vm.TotalRecords = 0
                }
                vm.isLoading = false;
            })
        },
        Close: function () {
            var vm = this;
            vm.$parent.GetDropDownData("HR_QUEST", vm.QField, vm.QFieldId, null);   
            vm.$parent.ShowManageAddQuestion = false;
        }
    }
}
</script>
